import {Component} from "@angular/core";
import {ToastController} from "ionic-angular";

/**
 * Generated class for the EducationAppComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'education-app',
  templateUrl: 'education-app.html'
})
export class EducationAppComponent {

  // text: string;

  constructor(public toastCtrl: ToastController) {
    console.log('Hello EducationAppComponent Component');
  }

  gotoEducationApp() {
    console.log("gotoDownloadPage");
    this.presentToast();
  }

  private presentToast() {
    let toast = this.toastCtrl.create({
      message: '以后会跳转教育APP下载页面，或者直接下载教育APP',
      duration: 3000
    });
    toast.present();
  }
}



